.meRightImgContainer{
    width: 350px;
    height: 450px;
    overflow: hidden;
}

.meRightImgContainer img{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.card{
    background: #ffffff;
    border-radius: 2px;
    box-sizing: border-box;
    width: 100%;
}
.skillContainer{
    width: 80px;
    height: 80px;
    overflow: hidden;
}
.socialConnect{
    background: #ffffff;
    width: 300px;
    height: 40px;
    display: flex;
    justify-content: center;
    border-radius: 20px;
    align-items: center;
}

.socialConnect .socialLink{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    overflow: hidden;
    border: 5px solid #ffffff;
    margin: 0 2px;
    transition: -webkit-transform .8s ease-out;
    transition: transform .8s ease-out;
    transition: transform .8s ease-out, -webkit-transform .8s ease-out;
}

.socialConnect .socialLink img{
    max-width: 100%;
    max-width: 100%;
}

.socialConnect .socialLink :hover{
-webkit-transform: scale(1.2);
        transform: scale(1.2);
box-shadow: 0 0 10px 0 #777;
}

